import logo from './logo.svg';
import './App.css';
import { Tab, Tabs } from "@material-ui/core"
import React from 'react'

function App() {

  const [tabVal, setTab] = React.useState(0)


  const tabChange = (event, newValue) => {
    setTab(newValue);
  };


  return (
    <div className="App">
      <header className="App-header">
        <Tabs
          value={tabVal}
          onChange={tabChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
          aria-label="TODO">
            <Tab label="Home"/>
            <Tab label="Resume"/>
            <Tab label="About Me"/>
            <Tab label="Contact me"/>
        </Tabs>
        
      </header>
    </div>
  );
}

export default App;
